// Visual Generation Service
// Main service for generating character portraits and visuals

import { CharacterVisualData, PortraitOptions, VisualGenerationResult, VisualPlugin } from './types';
import { assetManager, ensureAssetsLoaded, getFallbackSpecies, getFallbackArchetype } from './assets';
import { getClassVisualTheme, generateDefaultAppearance } from './classmap';
import { createCharacterRandom, ColorVariations } from './seed';
import { renderCharacterToSVG } from './renderer2d';
import { optimizeSvg } from './svgUtils';

const portraitCache: Record<string, string> = {};

class VisualService {
    private plugins: Map<string, VisualPlugin> = new Map();
    private initialized = false;

    /**
     * Initialize the visual service
     */
    async initialize(): Promise<void> {
        if (this.initialized) return;

        try {
            await ensureAssetsLoaded();
            this.registerDefaultPlugins();
            this.initialized = true;
            console.log('Visual service initialized');
        } catch (error) {
            console.error('Failed to initialize visual service:', error);
            throw error;
        }
    }

    /**
     * Register default visual plugins
     */
    private registerDefaultPlugins(): void {
        // 2D SVG renderer (primary)
        this.registerPlugin({
            name: '2d-svg-renderer',
            version: '1.0.0',
            render: this.render2DSVGPortrait.bind(this),
            supports: () => true // Supports all species/archetypes
        });

        // Placeholder text renderer (fallback)
        this.registerPlugin({
            name: 'text-placeholder',
            version: '1.0.0',
            render: this.renderTextPlaceholder.bind(this),
            supports: () => true
        });

        // TODO: Add Canvas, and 3D renderers in future updates
    }

    /**
     * Register a visual plugin
     */
    registerPlugin(plugin: VisualPlugin): void {
        this.plugins.set(plugin.name, plugin);
    }

    /**
     * Generate character portrait
     */
    async generatePortrait(
        characterData: CharacterVisualData,
        options: PortraitOptions = { size: 'medium', format: 'svg' }
    ): Promise<VisualGenerationResult> {
        const startTime = performance.now();

        try {
            if (!this.initialized) {
                await this.initialize();
            }

            // Enhance character data with generated defaults
            const enhancedData = this.enhanceCharacterData(characterData);

            // Find suitable plugin
            const plugin = this.findBestPlugin(enhancedData.species, enhancedData.archetype);
            if (!plugin) {
                throw new Error('No suitable visual plugin found');
            }

            // Generate the visual
            const context = this.createRenderContext(options);
            const result = await plugin.render(enhancedData, context);

            console.log(' VisualService: Plugin render result:', {
                resultType: typeof result,
                isString: typeof result === 'string',
                length: typeof result === 'string' ? result.length : 'N/A',
                preview: typeof result === 'string' ? result.substring(0, 100) + '...' : result,
                isSVG: typeof result === 'string' && result.includes('<svg')
            });

            const renderTime = performance.now() - startTime;

            const finalResult = {
                success: true,
                data: result,
                metadata: {
                    renderTime,
                    cacheKey: this.generateCacheKey(characterData, options),
                    assets: this.getUsedAssets(enhancedData)
                }
            };

            console.log(' VisualService: Final result being returned:', {
                success: finalResult.success,
                hasData: !!finalResult.data,
                dataType: typeof finalResult.data,
                renderTime: finalResult.metadata.renderTime
            });

            return finalResult;

        } catch (error) {
            const renderTime = performance.now() - startTime;
            return {
                success: false,
                error: error instanceof Error ? error.message : 'Unknown error',
                metadata: {
                    renderTime,
                    assets: []
                }
            };
        }
    }

    /**
     * Enhance character data with generated defaults
     */
    private enhanceCharacterData(data: CharacterVisualData): CharacterVisualData {
        const rng = createCharacterRandom(data);

        // Generate appearance if missing
        const appearance = data.appearance || {};
        const defaultAppearance = generateDefaultAppearance(data.species, data.archetype);

        // Use seed-based generation for missing values
        const colorVariations = ColorVariations.forSpecies(data.species, rng);

        return {
            ...data,
            appearance: {
                ...defaultAppearance,
                ...appearance,
                // Fill in missing color values with seeded variations
                skinTone: appearance.skinTone || colorVariations.skinTone,
                hairColor: appearance.hairColor || colorVariations.hairColor,
                eyeColor: appearance.eyeColor || colorVariations.eyeColor
            },
            style: data.style || {
                artStyle: 'stylized',
                colorScheme: 'natural'
            }
        };
    }

    /**
     * Find best plugin for character
     */
    private findBestPlugin(species: string, archetype: string): VisualPlugin | null {
        const pluginArray = Array.from(this.plugins.values());
        for (const plugin of pluginArray) {
            if (plugin.supports(species, archetype)) {
                return plugin;
            }
        }
        return null;
    }

    /**
     * Create render context
     */
    private createRenderContext(options: PortraitOptions) {
        return {
            options,
            // Canvas/SVG context will be added by specific renderers
        };
    }

    /**
     * Generate cache key for portrait
     */
    private generateCacheKey(data: CharacterVisualData, options: PortraitOptions): string {
        const keyData = {
            name: data.name,
            species: data.species,
            archetype: data.archetype,
            level: data.level,
            appearance: data.appearance,
            size: options.size,
            format: options.format
        };
        return btoa(JSON.stringify(keyData));
    }

    /**
     * Get assets used for character
     */
    private getUsedAssets(data: CharacterVisualData): string[] {
        const assets: string[] = [];

        // Get class-specific assets
        const theme = getClassVisualTheme(data.archetype);
        if (theme?.preferredAssets) {
            Object.values(theme.preferredAssets).flat().forEach(asset => {
                assets.push(asset);
            });
        }

        return assets;
    }

    /**
     * 2D SVG renderer (main renderer)
     */
    private async render2DSVGPortrait(
        data: CharacterVisualData,
        context: any
    ): Promise<string> {
        try {
            return await renderCharacterToSVG(data, context.options);
        } catch (error) {
            console.warn('2D SVG renderer failed, falling back to placeholder:', error);
            return this.renderTextPlaceholder(data, context);
        }
    }

    /**
     * Text placeholder renderer (fallback)
     */
    private async renderTextPlaceholder(
        data: CharacterVisualData,
        context: any
    ): Promise<string> {
        const { size } = context.options;
        const dimensions: Record<string, number> = {
            small: 64,
            medium: 128,
            large: 256
        };

        const dim = dimensions[size as string] || 128;

        // Try to get theme colors, with fallbacks
        const theme = getClassVisualTheme(data.archetype);
        const primaryColor = theme?.colorPalette.primary || '#4a5568';
        const secondaryColor = theme?.colorPalette.secondary || '#718096';

        // Try species fallback for colors
        let speciesColor = primaryColor;
        if (data.species === 'Crystalborn') speciesColor = '#9f7aea';
        else if (data.species === 'Sylvanborn') speciesColor = '#38a169';
        else if (data.species === 'Nightborn') speciesColor = '#553c9a';
        else if (data.species === 'Stormcaller') speciesColor = '#3182ce';
        else if (data.species === 'Draketh') speciesColor = '#e53e3e';
        else if (data.species === 'Alloy') speciesColor = '#718096';
        else if (data.species === 'Voidkin') speciesColor = '#2d3748';

        // Create a more attractive procedural SVG
        return `
      <svg width="${dim}" height="${dim}" viewBox="0 0 ${dim} ${dim}" xmlns="http://www.w3.org/2000/svg">
        <defs>
          <linearGradient id="bgGrad" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style="stop-color:${speciesColor};stop-opacity:0.1"/>
            <stop offset="100%" style="stop-color:${secondaryColor};stop-opacity:0.2"/>
          </linearGradient>
        </defs>
        <rect width="${dim}" height="${dim}" fill="url(#bgGrad)"/>
        <circle cx="${dim / 2}" cy="${dim * 0.35}" r="${dim * 0.12}" fill="${speciesColor}" opacity="0.6"/>
        <rect x="${dim * 0.3}" y="${dim * 0.5}" width="${dim * 0.4}" height="${dim * 0.35}" fill="${speciesColor}" opacity="0.4" rx="4"/>
        <text x="${dim / 2}" y="${dim * 0.85}" text-anchor="middle" font-family="Arial, sans-serif" font-size="${dim * 0.08}" font-weight="bold" fill="${speciesColor}">
          ${data.name || 'Character'}
        </text>
        <text x="${dim / 2}" y="${dim * 0.93}" text-anchor="middle" font-family="Arial, sans-serif" font-size="${dim * 0.06}" fill="${secondaryColor}" opacity="0.8">
          ${data.species} ${data.archetype}
        </text>
      </svg>
    `.trim();
    }

    /**
     * Get service statistics
     */
    getStats() {
        return {
            initialized: this.initialized,
            plugins: this.plugins.size,
            pluginNames: Array.from(this.plugins.keys()),
            assetStats: assetManager.getStats()
        };
    }

    /**
     * Check if service is ready
     */
    isReady(): boolean {
        return this.initialized && assetManager.isLoaded();
    }
}

// Singleton instance
export const visualService = new VisualService();

// Convenience functions
export async function generateCharacterPortrait(
    characterData: CharacterVisualData,
    options?: PortraitOptions
): Promise<VisualGenerationResult> {
    return visualService.generatePortrait(characterData, options);
}

export async function initializeVisualSystem(): Promise<void> {
    await visualService.initialize();
}

export function isVisualSystemReady(): boolean {
    return visualService.isReady();
}

/**
 * Bind a generated portrait to a character object (as shown in patch example)
 * This function generates a portrait and sets the portraitUrl property
 */
export async function bindPortraitToCharacter(
    character: any,
    options: {
        body?: string;
        palette?: string;
        size?: { width: number; height: number };
    } = {}
): Promise<any> {
    try {
        // Convert character to visual data format
        const visualData: CharacterVisualData = {
            name: character.name || 'Character',
            species: character.species || 'Human',
            archetype: character.archetype || 'Warrior',
            level: character.level || 1,
            appearance: character.appearance || {}
        };

        // Generate portrait with specified options
        const portraitOptions: PortraitOptions = {
            size: (options.size?.width || 128) > 200 ? 'large' : (options.size?.width || 128) > 100 ? 'medium' : 'small',
            format: 'svg', // Can be extended to support PNG data URLs in future
            quality: 'medium'
        };

        const result = await generateCharacterPortrait(visualData, portraitOptions);

        if (result.success && result.data) {
            // For SVG, we can convert to data URL if needed
            let portraitUrl = result.data as string;

            if (typeof result.data === 'string' && result.data.startsWith('<svg')) {
                // Convert SVG to data URL
                const svgBlob = new Blob([result.data], { type: 'image/svg+xml' });
                portraitUrl = `data:image/svg+xml;base64,${btoa(result.data)}`;
            }

            // Bind to character object
            character.portraitUrl = portraitUrl;

            console.log(`Portrait bound to ${character.name}: ${portraitUrl.substring(0, 50)}...`);
        } else {
            console.warn(`Failed to generate portrait for ${character.name}:`, result.error);
        }

        return character;
    } catch (error) {
        console.error('Error binding portrait to character:', error);
        return character;
    }
}

/**
 * Add this utility function
 */
export function clearPortraitCache(): void {
    Object.keys(portraitCache).forEach(key => delete portraitCache[key]);
    console.log('Portrait cache cleared');
}